
export const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../views/Home.vue'),
    children:[
      {
        path:'HomePage',
        name:'homePage',
        component:()=>import('../views/Home/HomePage.vue')
      }, 
      {
        path: 'Main',
        name: 'main',
        component: () => import('../views/Home/Main.vue'),
        children:[
          {
            path: 'Subclass',
            name: 'subclass',
            component: () => import('../views/Subclass.vue')
          }
        ]
      },
      {
        path:'My',
        name:'my',
        component:()=>import('../views/Home/My.vue')
      }, 
      {
        path: '/',
        redirect: {
          name: 'homePage'
        }
      }
    ],
    
  },
  {
    path: '/about',
    name: 'About',
    component: () => import('../views/About.vue')
  },
  {
    path: '/Search',
    name: 'search',
    component: () => import('../views/Search.vue')
  },
  {
    path: '/Detail',
    name: 'detail',
    component: () => import('../views/Detail.vue')
  },
  {
    path: '/Sancan',
    name: 'sancan',
    component: () => import('../views/Sancan.vue'),
    children:[
      {
        path: 'Zao',
        name: 'zao',
        component: () => import('../views/sancan/Zao.vue'),
      },
      {
        path: 'Zhong',
        name: 'zhong',
        component: () => import('../views/sancan/Zhong.vue'),
      },{
        path: 'Wan',
        name: 'wan',
        component: () => import('../views/sancan/Wan.vue'),
      }
    ]
  },
  {
    path:'/browsingHistory',
    name:'browsingHistory',
    component:()=>import('../views/BrowsingHistory.vue')
  },
  
  
  
]