export const mutations = {
    //浏览记录
    changeData(state, data) {
        // 如果浏览记录重复则将浏览记录移至第一位
        data.check = false
        for (let i = 0; i < state.products.length; i++) {
            if (state.products[i].key == data.key) {
                state.products.splice(i, 1)
                state.products.unshift(data)
                return
            }
        }
        state.products.unshift(data)
    },
    //浏览记录删除
    changeProducts(state, data) {
        //清空products
        state.products = []
        //将更新后的数据传入products
        for (let i = 0; i < data[0].length; i++) {
            state.products.push(data[0][i])
        }

    },
    //记录列表数据
    listData(state, data) {
        state.listes.unshift(data)
    },
    //记录搜索结果
    searchData(state, data) {
        state.searchResult.unshift(data)

    },
    //记录搜索的关键字
    recordData(state, data) {
        state.record.unshift(data)
    },
    //删除搜索记录
    emptyData(state) {
        state.record = []
    },
    //记录about侧栏下标
    Active(state, data) {
        state.active = data

    },
    //记录三餐导航下标
    FoodActive(state, data) {
        state.foodActive = data

    }

}