import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import axios from 'axios'
import VueAxios from 'vue-axios'
import 
{ Button, Tabbar, TabbarItem, Search, Grid, 
  GridItem, PullRefresh, List, Cell, Icon,
   NavBar, Tag,Sidebar, SidebarItem  ,Toast,
   Tab, Tabs ,Checkbox, CheckboxGroup ,Rate } 
from 'vant';

import 'lib-flexible'

Vue.use(VueAxios, axios)
Vue
  .use(Button)
  .use(Tabbar)
  .use(TabbarItem)
  .use(Search)
  .use(Grid)
  .use(GridItem)
  .use(PullRefresh)
  .use(List)
  .use(Cell)
  .use(NavBar)
  .use(Tag)
  .use(Icon)
  .use(Sidebar)
  .use(SidebarItem)
  .use(Toast)
  .use(Tab)
  .use(Tabs)
  .use(Checkbox)
  .use(CheckboxGroup)
  .use(Rate)
Vue.config.productionTip = false


axios.defaults.baseURL = '/api'
Vue.prototype.appkey = '4c9ddbe3a3dcd8a0'

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
